.chart-bar {
  font-size: 1.4rem; }
  .chart-bar table {
    width: 100%;
    margin: 0;
    padding: 0;
    table-layout: auto; }
  .chart-bar tr, .chart-bar td {
    border: 0; }
  .chart-bar td {
    width: auto;
    padding: 0;
    margin: 0;
    white-space: nowrap;
    line-height: 0; }
  .chart-bar td:first-child {
    padding-right: 5px; }
  .chart-bar td:last-child {
    width: 100%; }
  .chart-bar .label, .chart-bar .bar {
    display: inline-block;
    margin: 0;
    padding: 0;
    height: 2rem;
    line-height: 2rem; }
  .chart-bar .label {
    color: #a7a9ac; }
  .chart-bar .bar {
    position: relative;
    background: #7fc9bd;
    text-align: right; }
  .chart-bar .pct {
    color: #fff;
    padding-right: 0.5rem; }
  .chart-bar .zero .pct {
    color: #a7a9ac; }
  .chart-bar .align-r .pct {
    position: absolute;
    color: #a7a9ac;
    left: 100%;
    padding-right: 0;
    padding-left: 0.25rem; }
  @media print {
    .chart-bar .label,
    .chart-bar .zero .pct,
    .chart-bar .align-r .pct {
      color: #464646; } }



