.Leaflet-Map {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  /* Leaflet style overrides */ }
  .Leaflet-Map .map {
    position: relative;
    width: 100%;
    height: 100%; }
  .Leaflet-Map .laarts-cluster {
    background: rgba(125, 125, 125, 0.6);
    border-radius: 20px; }
    .Leaflet-Map .laarts-cluster > div {
      width: 30px;
      height: 30px;
      margin-left: 5px;
      margin-top: 5px;
      text-align: center;
      border-radius: 15px; }
      .Leaflet-Map .laarts-cluster > div span {
