.SelectBox {
  position: relative;
  margin-bottom: 1px; }
  .SelectBox:last-child {
    margin-bottom: 0; }
  .SelectBox .tools {
    display: table;
    width: 100%; }
  .SelectBox .action {
    display: table-cell;
    vertical-align: middle; }
  .SelectBox .action:first-child {
    width: 100%; }
  .SelectBox .blocker {
    position: absolute;
    display: none;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    background: rgba(255, 255, 255, 0);
    pointer-events: none; }
  .SelectBox .VirtualizedSelectOption {
    font-size: 1.2rem;
    color: #333;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: default; }
  .SelectBox .VirtualizedSelectFocusedOption {
    background-color: rgba(127, 201, 189, 0.3); }
  .SelectBox .Select-control {
    font-size: 1.4rem; }
  .SelectBox .Select-placeholder,
  .SelectBox .Select--single > .Select-control .Select-value {
    padding-right: 18px; }
  .SelectBox button {
    display: block;
    border: 0;
    background: transparent;
    -webkit-box-shadow: none;
            box-shadow: none;
    margin: 0 0 0 0.5rem;
    padding: 0;
    line-height: 1;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    outline: none; }
    .SelectBox button[disabled] {
      pointer-events: none;
      opacity: 0.6; }
  .SelectBox .Icon svg {
    fill: #a7a9ac; }
  .SelectBox .Icon:hover svg {
    fill: #7fc9bd; }
  .SelectBox.active .Select-control {
    border-color: #7fc9bd; }
  .SelectBox.active .Icon.view svg {
    fill: #7fc9bd; }
  .SelectBox.disabled .blocker {
    display: block; }
  .SelectBox.disabled .action {
    pointer-events: none; }
  .SelectBox.is-static .Select-value {
    color: #333; }
  .SelectBox.is-static .action.close button {
    visibility: hidden !important;
    pointer-events: none !important;
    opacity: 0 !important; }



