.Explorer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #fff;
  z-index: 2; }
  .Explorer .tab-content {
    padding: 0 1rem;
    height: 100%;
    overflow: scroll;
    overflow-x: hidden; }
  .Explorer .module-data {
    margin-top: 1rem;
    white-space: initial;
    line-height: 1.2; }
    .Explorer .module-data:last-child {
      margin-bottom: 1rem; }
    .Explorer .module-data h4 {
      position: relative;
      margin: 0 0 0.7rem;
      padding: 0;
      color: #f4981e;
      font-size: 1.4rem;
      line-height: 1; }
    .Explorer .module-data p.simple {
      margin: 0;
      padding: 0;
      color: #a7a9ac;
      font-size: 1.4rem; }
    .Explorer .module-data p.no-wrap {
      white-space: initial;
      line-height: 1.3; }
    .Explorer .module-data p.note {
      margin: 0.85rem 0 0;
      padding: 0;
      font-size: 1.2rem;
      line-height: 1.2;
      color: #a7a9ac;
      white-space: initial;
      font-style: italic; }
    .Explorer .module-data a {
      color: #7fc9bd;
      text-decoration: none; }
      .Explorer .module-data a:hover {
        text-decoration: underline; }
    @media print {
      .Explorer .module-data p.note,
      .Explorer .module-data p.simple {
        color: #464646; } }
  .Explorer .module-data ~ .module-data {
    margin-top: 2rem; }



