.chart-list {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #a7a9ac;
  font-size: 1.4rem; }
  .chart-list li {
    padding: 0.5rem 0; }
    .chart-list li:first-child {
      padding-top: 0; }
    .chart-list li:last-child {
      padding-bottom: 0; }
  .chart-list .label {
    text-transform: uppercase; }
  .chart-list .value {
    color: #7fc9bd;
    font-weight: bold; }
  @media print {
    .chart-list {
      color: #464646; } }



